declare const wx: any;
declare const my: any;
declare const tt: any;
declare const dd: any;

/**
 *  SDK 
 */
interface SDK {
  request: Function;
  httpRequest?: Function; // 
  getSystemInfoSync: Function;
  onError?: Function;
  onUnhandledRejection?: Function;
  onPageNotFound?: Function;
  onMemoryWarning?: Function;
  getLaunchOptionsSync?: Function;
}

/**
 *  
 */
type AppName = "wechat" | "alipay" | "bytedance" | "dingtalk" | "unknown";

/**
 *  SDK
 */
const getSDK = () => {
  let currentSdk: SDK = {
    // tslint:disable-next-line: no-empty
    request: () => {},
    // tslint:disable-next-line: no-empty
    httpRequest: () => {},
    // tslint:disable-next-line: no-empty
    getSystemInfoSync: () => {},
  };

  if (typeof wx === "object") {
    // 
    // tslint:disable-next-line: no-unsafe-any
    currentSdk = wx;
  } else if (typeof my === "object") {
    // 
    // tslint:disable-next-line: no-unsafe-any
    currentSdk = my;
  } else if (typeof tt === "object") {
    // 
    // tslint:disable-next-line: no-unsafe-any
    currentSdk = tt;
  } else if (typeof dd === "object") {
    // 
    // tslint:disable-next-line: no-unsafe-any
    currentSdk = dd;
  } else {
    throw new Error("sentry-miniapp ");
  }

  return currentSdk;
};

/**
 * 
 */
const getAppName = () => {
  let currentAppName: AppName = "unknown";

  if (typeof wx === "object") {
    currentAppName = "wechat";
  } else if (typeof my === "object") {
    currentAppName = "alipay";
  } else if (typeof tt === "object") {
    currentAppName = "bytedance";
  } else if (typeof dd === "object") {
    currentAppName = "dingtalk";
  }

  return currentAppName;
};

const sdk = getSDK();
const appName = getAppName();

export { sdk, appName };
